<script>
import { useI18n } from "vue-i18n";
import { defineComponent } from "vue";
import posts from "../data/news.json";
import LiteYouTubeEmbed from "vue-lite-youtube-embed";
import NavBar from "../components/NavBar.vue";
import "vue-lite-youtube-embed/dist/style.css";
import axios from "axios";

export default defineComponent({
  name: "App",
  props: {
    lang: {
      type: String,
      required: true,
      default: 'en'
    },
  },
  data() {
    return {
      nrOfNewsItems: 2,
      posts: posts,
      subscription: {
        processing: false,
        submitted: false,
        errorMsg: "",
        model: {
          email: ""
        }
      },
      gigs: [
        
      ]
    };
  },
  setup() {
    return useI18n();
  },
  components: {
    LiteYouTubeEmbed, NavBar
  },
  mounted() {
    document.title = this.t("title");
  },
  created() {
    this.$i18n.locale = this.lang; //this.$route.params.lang;
    this.getGigs();
  },
  methods: {
    lessNews() {
      this.nrOfNewsItems -= 2;
    },
    moreNews() {
      this.nrOfNewsItems += 2;
    },
    open(uri) {
      window.open(uri, "_blank").focus();
    },
    toAlbum() {
      window.open("./abracadabra", "_blank").focus();
    },
    formatDate(date) {
      const d = new Date(date);
      const weekday = this.t("weekdays." + d.getDay())
      const month = this.t("months." + d.getMonth())
      const monthday = d.getDate();
      return this.lang == "nl" ? weekday + " " + monthday + " " + month : weekday + ", " + month + " " + monthday;
    },
    clearSubscription() {
      let input = document.getElementById("subscription_input");
      input.classList.remove("success");
      input.focus();
      this.subscription.submitted = false;
      this.subscription.processing = false;
      this.subscription.model.email = "";
    },
    noGigs() {
      return this.gigs == undefined || this.gigs.length == 0;
    },
    getGigs() {
      axios.get("https://api.songkick.com/api/3.0/artists/10179135/calendar.json?apikey=VgOeG4TUuURr2Lys").then((data) => {
          this.gigs = data.data.resultsPage.results.event;
        })
        .catch(() => {
          this.gigs = [];
        });
    },
    subscribe() {
      this.subscription.processing = true;
      document.activeElement.blur();
      axios
        .post(
          "https://europe-west1-basbeelen-com.cloudfunctions.net/newsletter",
          JSON.stringify(this.subscription.model),
          {
            headers: {
              "Content-Type": "application/json",
            },
          }
        )
        .then(() => {
          this.subscription.submitted = true;
          document.getElementById("subscription_input").classList.add("success");
          setTimeout(this.clearSubscription, 1500);
        })
        .catch((error) => {
          this.subscription.errorMsg = error.response.data;
          setTimeout(() => {
            let input = document.getElementById("subscription_input");
            input.setSelectionRange(0, input.value.length);
            input.focus();
            this.subscription.errorMsg = "";
            this.subscription.processing = false;
          }, 2000);
        });
    }
  },
  watch: {
    $route() {
      this.$i18n.locale = this.$route.params.lang;
      document.title = this.t("title");
    },
  },
});
</script>

